.settings {
  display: block;
  flex-direction: column;
  background: #fff;
  padding: 30px;
  border-radius: 6px;
  margin: 0 auto;
  margin-top: 50px;
  width: 40%;
  border-radius: 6px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
}
.settings .imgContainer {
  display: flex;
  margin-bottom: 40px;
  justify-content: center;
}
.settings .imgContainer img {
  border-radius: 4px;
  height: 100px;
}
.settings .btnGrp {
  display: flex;
}
.settings button {
  border-radius: 6px;
  padding: 10px;
  width: fit-content;
  font-size: 16px;
  border-radius: 3px;
  margin-top: 20px;
}
.settings button:hover {
  cursor: pointer;
}
.settings button:focus {
  outline: none;
}
.settings .editBtn {
  border: 1px solid #FF7043;
  box-sizing: border-box;
  color: #FF7043;
}
.settings .saveBtn {
  background: #FF7043;
  color: #fff;
  border: none;
  padding: 10px 20px;
  margin-right: 10px;
}
.settings .saveBtn:disabled {
  cursor: not-allowed;
}
.settings .goBack {
  color: #FF7043;
  margin-top: 7px;
  font-size: 13px;
  text-decoration: none;
}
.settings .goBack:hover {
  cursor: pointer;
  text-decoration: underline;
}
.settings .field {
  margin-bottom: 10px;
}
.settings .field .fieldLabel {
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 22px;
  color: #9e9e9e;
  padding: 5px 0;
}
.settings .field input {
  margin-top: 6px;
  width: 100%;
  height: 35px;
  font-size: 16px;
  padding-left: 10px;
  border: 1px solid rgba(0, 0, 0, 0.12);
  border-radius: 6px;
}
input[type=file]::file-selector-button {
  background-color: #fff;
  color: #000;
  border: 0px;
  border-right: 1px solid #e5e5e5;
  border-radius: 6px 0 0 6px;
  padding: 10px 15px;
  margin-right: 20px;
  margin-left: -10px;
  transition: .1s;
}

input[type=file]::file-selector-button:hover {
  cursor: pointer;
  color: #FF7043;
  background-color: #F5F5F5;
  border: 0px;
  border-right: 1px solid #FF7043;
}